<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" color="#08507e" app>
      <v-list-item>
        <v-list-item-avatar>
          <v-img src="favicon.ico"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title class="title white--text">
            Welcome
          </v-list-item-title>
          <v-list-item-subtitle class="white--text">
            {{ user_name }}
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense nav>
        <v-list-item
          v-for="item in allLinks"
          :key="item.title"
          :to="item.to"
          link
        >
          <v-list-item-icon>
            <v-icon color="white">{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title class="white--text">{{
              item.title
            }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <!--  -->
    </v-navigation-drawer>

    <v-app-bar app>
      <v-app-bar-nav-icon
        @click="drawer = !drawer"
        color="black"
      ></v-app-bar-nav-icon>

      <v-toolbar-title class="black--text"
        >SOLID WASTE MANAGEMENT SYSTEM</v-toolbar-title
      >
      <!-- <input v-model="userToken" type="text" /> -->
      <v-spacer></v-spacer>
      <v-btn icon>
        <v-icon @click="logout">mdi-logout-variant</v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <!--  -->
      <router-view></router-view>
    </v-main>
    <v-footer padless>
      <v-col class="text-center" cols="12">
        {{ new Date().getFullYear() }} — <strong>SWMS</strong>
      </v-col>
    </v-footer>
  </v-app>
</template>

<script>
var url = window.location;

let searchParams = new URLSearchParams(url.search);
let iisToken = searchParams.get("token");
let iisTokenID = searchParams.get("token_id");
let origin = searchParams.get("origin");

import { mapActions, mapGetters } from "vuex";
import swal from "sweetalert";
export default {
  data: () => ({
    token: iisToken,
    token_id: iisTokenID,
    drawer: null,
    user_name: "",
  }),
  methods: {
    ...mapActions(["getIISToken", "getSidebar"]),
    logout() {
      swal({
        text: "Are you sure to end your session?",
        icon: "warning",
        buttons: true,
        dangerMode: true,
      }).then((willEnd) => {
        if (willEnd) {
          sessionStorage.clear();
          window.location.href = "https://iis.emb.gov.ph/embis/";
        }
      });
    },
  },
  computed: mapGetters(["allLinks"]),
  created() {
    this.getIISToken({
      token: this.token,
      id: this.token_id,
      origin: this.origin,
    });
    console.log(sessionStorage.getItem("access")),
      this.getSidebar({
        access_level: sessionStorage.getItem("access"),
      });
    this.user_name = sessionStorage.getItem("name");
  },
};
</script>