import con from './config'
import swal from 'sweetalert'
const state = {
    PlanList: [],
    currentRowNo: 0,
    Form2: []
}

const getters = {
    allAppPlanList: state => state.PlanList,
    tappgetCurrentrow: state => state.currentRowNo,
    allForm2: state => state.Form2
}

const actions = {
    async fetchAppPlanList({ commit }, row) {
        const region = sessionStorage.getItem('cregion');

        await con.get(`/PlanAppList/${row.limit}/${row.offset}/${region}/${row.status}`).then(function (res) {
            if (res.data['PlanList'].length > 0) {
                // commit data until last
                commit('getPlanList', res.data['PlanList']);
            } else {
                // minus limit if last data meet
                var rowoffset = row.offset - row.limit;
                if (rowoffset <= 0) {
                    rowoffset = 0;
                }
                commit('currentRow', rowoffset);
            }

        }).catch(error => {
            console.log(error);
            if (error.response.data.error == "Expired token") {
                sessionStorage.clear();
                window.location.href = "https://iis.emb.gov.ph/embis/";
            }
        })
    },
    async incrementApp({ commit }, row) {
        var limitoffset = parseInt(row.offset) + parseInt(row.limit);
        commit('currentRow', limitoffset);
    },
    async decrementApp({ commit }, row) {
        // decrement
        var limitoffset = parseInt(row.offset) - parseInt(row.limit);
        commit('currentRow', limitoffset);
        if (row.offset <= 0) {
            // commit to 0 if data is less than zero
            commit('currentRow', 0);
        }
    },
    async searchAppPlanList({ commit }, res) {
        const region = sessionStorage.getItem('cregion');
        var limit = parseInt(res.limit);
        await con.post('/SearchPlanAppList', {
            txtsearch: res.txtsearch,
            txtlimit: limit,
            region: region,
            status: res.status
        }).then(function (res) {
            commit('currentRow', res.data['offset']);
            commit('getPlanList', res.data['PlanList']);
        }).catch(error => {
            if (error.response.data.error == "Expired token") {
                sessionStorage.clear();
                window.location.href = "https://iis.emb.gov.ph/embis/";
            }
        })
    },

    async createForm2({ commit }, res) {
        console.log(res.indicator_com_1_1)
        await con.post('/CreateFormTwo',
            {
                header_id: res.header_id,
                indicator_com_1_1: res.indicator_com_1_1,
                indicator_rem_1_1: res.indicator_rem_1_1,
                indicator_com_1_2: res.indicator_com_1_2,
                indicator_rem_1_2: res.indicator_rem_1_2,
                indicator_com_1_3: res.indicator_com_1_3,
                indicator_rem_1_3: res.indicator_rem_1_3,
                indicator_com_1_4: res.indicator_com_1_4,
                indicator_rem_1_4: res.indicator_rem_1_4,
                memo_agreement: res.memo_agreement,
                contract: res.contract,
                disposal_area: res.disposal_area,

                indicator_com_2_1: res.indicator_com_2_1,
                indicator_rem_2_1: res.indicator_rem_2_1,

                indicator_com_3_1: res.indicator_com_3_1,
                indicator_com_3_2: res.indicator_com_3_2,
                indicator_rem_3_1: res.indicator_rem_3_1,

                indicator_com_4_1: res.indicator_com_4_1,
                indicator_rem_4_1: res.indicator_rem_4_1,

                indicator_com_5_1: res.indicator_com_5_1,
                indicator_rem_5_1: res.indicator_rem_5_1,
                indicator_com_5_2: res.indicator_com_5_2,
                indicator_rem_5_2: res.indicator_rem_5_2,

                indicator_com_6_1: res.indicator_com_6_1,
                indicator_rem_6_1: res.indicator_rem_6_1,

                indicator_com_7_1: res.indicator_com_7_1,
                indicator_rem_7_1: res.indicator_rem_7_1,
            }).then(function () {

                // commit("chapter", 2);
            }).catch(error => {
                if (error.response.data.error == "Expired token") {
                    sessionStorage.clear();
                    window.location.href = "https://iis.emb.gov.ph/embis/";
                } else {
                    var errorLog = JSON.stringify(error.response.data);
                    swal({
                        title: "Server Side Validation",
                        text: errorLog,
                        icon: "error",
                        dangerMode: true,
                    }).then((willContinue) => {
                        if (willContinue) {
                            commit("chapter", 1);
                        }
                    });
                }
            })

    },

    async fetchForm2({ commit }, res) {

        // sessionStorage.setItem("city_id", res.city);
        // sessionStorage.setItem("header_id", res.header_id);
        await con.get(`/GetForm2/${res.id}`
        ).then(function (res) {
            commit("getForm2", res.data['result']);
            // window.location.href = "#/plan?city=" + sessionStorage.getItem("city_id") + "&header_id=" + sessionStorage.getItem("header_id") + "&chapter=" + chapter;
        }).catch(error => {
            if (error.response.data.error == "Expired token") {
                sessionStorage.clear();
                window.location.href = "https://iis.emb.gov.ph/embis/";
            }
            else if (error.response.data.error == "Key may not be empty") {
                sessionStorage.clear();
                window.location.href = "#/eplan";
            } else {
                var errorLog = JSON.stringify(error.response.data);
                swal({
                    title: "Server Side Validation",
                    text: errorLog,
                    icon: "error",
                    dangerMode: true,
                }).then((willContinue) => {
                    if (willContinue) {
                        commit("chapter", chapter);
                    }
                });
            }
        })
    },



};

const mutations = {
    getPlanList: (state, PlanList) => (state.PlanList = PlanList),
    currentRow: (state, Irow) => (state.currentRowNo = Irow),
    getForm2: (state, c) => (state.Form2 = c)
}

export default {
    state,
    getters,
    actions,
    mutations
};